<template>
    <section>
        <div class="theme-primary partition-full">
            <span class="p-name text-white">Tickets</span>
            <div class="p-name text-white px-2">
                <select v-model="ViewTicketType" v-if="CompanyType != 'normal'"
                    class="form-control float-left customized_ddl" style="width:168px"
                    v-on:change="changeViewTicketType()">
                    <option value="">ALL Tickets</option>
                    <option value="FSM">Field Service Ticket</option>
                    <option value="TKT">Normal Tickets</option>
                </select>
                <select v-model="userstatus" name="ddlUserCurrentStatus" class="form-control customized_ddl"
                    id="ddlUserCurrentStatus" v-on:change="UserCurrentStatus()" style="width:150px;">
                    <option value="Busy">{{ $t('Busy') }}</option>
                    <option value="Offline">{{ $t('offline') }}</option>
                    <option value="Online">{{ $t('online') }}</option>
                </select>
            </div>
            <div class="p-actions legends_topbar float-right" v-show="CompanyType != 'normal'">
                <div class="clear showclear"></div>
                <a href="javascript:;"
                    style="display: flex;align-items: center;flex-direction: column;align-content: flex-end;justify-content: flex-start;flex-wrap: wrap;height:52px;"
                    id="pinUnpinId" :value="PinId" v-on:click="SavePinView();"
                    class="p-action-btn text-white align-items-center unpinscreenMap pinUnpinId" pinned="0" title="PIN">
                    <i aria-hidden="true" style="margin-top: -3px;position: absolute;"
                        class="fa fa-thumb-tack"></i><span id="spnPin">{{ $t('PIN') }}</span>
                </a>
                <div class="p-actions legends_topbar float-right">
                    <div class="p-action-map collapse_map">
                        <a href="javascript:;" class="e-c-map p-action-btn text-white align-items-center d-flex"
                            v-on:click="ShowHideMap">
                            <i id="icnToggleMap" class="fa fa-angle-double-up pr-2"></i>
                            <span id="spnToggleMap">{{ mapHeaderText }}</span></a>
                    </div>
                </div>
            </div>
        </div>
        <ManageMap class="row" :mapType="MapType" v-if="isShowMap" mapContainer="indexMapDiv" :latLng="latLng"
            :Data="mapObj" :key="componentKey" :config="config"></ManageMap>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" v-show="IsTicketingView" :IsShowAction="true" ModuleName="Ticketing"
            SubModuleCode="Ticketing" IdentityColumn="ticket_id" :listType="ListView" :ListData="TicketList"
            :ViewList="ViewList" v-on:ApplyView="OnApplyView" :HeaderData="Headers" :widgets="widgets"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :RenderRowActionMethod="renderActions" :PageSize="PageSize" :ListDataCallBackFunction="fetchRecords"
            :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :LegendArray="LegendArray"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata"
            v-on:RefreshManageView="fetchRecords">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <a class="inactive-disabled">
                        <span v-bind:class="getStatusClassList(data.row.status_id, 'status_id')"
                            v-if="data.row.status_id != 'Active' && data.row.status_id != 'ApprovalPending'">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ticket_subject'">
                    <!-- <RouterLink :title="data.row[data.column.COLUMN_NAME]"
                        :to="'/Ticketing/Manage/' + data.row.str_ticket_id, query: {  TicketNumber: row.SA_Related_Object_Id } 
                        data-toggle-tooltip="tooltip">
                        {{ data.row[data.column.COLUMN_NAME] }}</RouterLink> -->
                    <RouterLink :title="data.row[data.column.COLUMN_NAME]"
                        :to="{ path: '/Ticketing/Manage/' + data.row.str_ticket_id, query: { status_id: data.row.status_id } }"
                        data-toggle-tooltip="tooltip">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </RouterLink>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'totalcomments'">
                    <a href="javascript:;" class="text-dark" v-on:click="OpenReplyTicketPopup(data)"><i
                            aria-hidden="true" class="fa fa-commenting-o"></i><span class="noti-circle noti-green">{{
                    data.row[data.column.COLUMN_NAME] }}</span></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'attachments_count'">
                    <a href="javascript:;" class="text-dark" @click="openAttachmentPopUp(data)"><i aria-hidden="true"
                            class="fa fa-paperclip"></i><span class="noti-circle noti-blue"> {{
                    data.row[data.column.COLUMN_NAME] }}</span></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'no_of_notes'">
                    <a href="javascript:;" class="text-dark viewnotes" @click="openNotesPopUp(data)"><i
                            aria-hidden="true" class="fa fa-file-text-o"></i><span class="noti-circle noti-orange">{{
                    data.row[data.column.COLUMN_NAME] }}</span></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Description'">
                    <div class="description-hover">
                        <span class="description-text" v-html="TruncateStr(data.row.comment, 30)"></span>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Remaining_Time'">
                    <div class="Remaining_Time-hover">
                        <span :class="getTimerTextClass(data.row.remainingTime)"
                            v-html="TruncateStr(data.row.remainingTime, 30)"></span>
                    </div>
                </template>

            </template>
            <template v-slot:customcard="{ data }">
                <div v-bind:style="SetTicketColor(data.row.color_code)"
                    v-bind:class="{ 'card-box': true, 'fsm-ticket': data.row.is_by_recurring == true }">
                    <div class="card-number">
                        <div class="custom-control custom-checkbox custom-control-inline" style="">
                            <input :id="data.row.TicketNumber" class="chkItems custom-control-input"
                                @change="ForCheckBoxClear($event)" v-bind:value="data.row.ticket_id" type="checkbox"
                                :disabled="data.row.status_code == 'TICKET_DRAFT'" />
                            <label :for="data.row.TicketNumber" type="checkbox" class="custom-control-label"></label>
                        </div> {{ data.row.TicketNumber }}
                        <span v-if="data.row.external_ticket_number != null"
                            style=" font-size: 12px; color: #bfbfbf;">({{
                    data.row.external_ticket_number }})</span>
                        <span v-if="data.row.is_field_service == 'FSM'" style="font-weight: normal"
                            class="badge badge-info float-right">FSM</span>
                        <span v-if="data.row.is_field_service == 'Normal'" style="font-weight: normal"
                            class="badge badge-primary float-right">Normal</span>
                    </div>
                    <div class="card-detail-main filterscrollForCardView">
                        <div v-if="data.row.ticket_subject != ''" v-bind:data-isfsm="CompanyType" class="card-detail">
                            <em class="fa fa-newspaper-o" aria-hidden="true"></em> <span>{{ data.row.ticket_subject
                                }}</span>
                        </div>
                        <div v-if="data.row.Formated_address != ''" class="card-detail"><em class="fa fa-map-marker"
                                aria-hidden="true"></em> <span>{{ data.row.Formated_address }} {{ data.row.State
                                }}</span>
                        </div>
                        <div class="card-detail" v-if="data.row.is_field_service == 'FSM'"><em class="fa fa-wrench"
                                aria-hidden="true"></em> <span>{{ data.row.TypeofWork != '' ? data.row.TypeofWork :
                    '--' }}</span></div>
                        <div class="card-detail" v-if="data.row.is_field_service == 'Normal'"><em class="fa fa-users"
                                aria-hidden="true"></em> <span>{{ data.row.ticket_department_id != '' ?
                    data.row.ticket_department_id : '--' }}</span></div>
                        <div class="card-detail" v-if="(data.row.assigned_to != '' && data.row.assigned_to != null)">
                            <i class="fa fa-user-o" aria-hidden="true"></i><span>{{ data.row.assigned_to }}</span>
                        </div>
                        <div class="card-detail"
                            v-if="data.row.remainingTime != null && data.row.remainingTime != '' && data.row.remainingTime != '--'">
                            <em class="fa fa-clock-o" aria-hidden="true"></em><span
                                :class="getTimerTextClass(data.row.remainingTime)">{{ data.row.remainingTime }}</span>
                        </div>

                        <div class="card-detail" v-else-if="(data.row.assigned_to == null)">
                            <em class="fa fa-user-o" aria-hidden="true"></em><span>&#8210; &#8210;</span>
                        </div>
                        <div class="card-detail" v-if="data.row.resolve_time != null"><em class="fa fa-calendar"
                                aria-hidden="true"></em> <span>{{ $options.filters.formatDateTime(data.row.resolve_time)
                                }}</span>
                        </div>
                        <div class="card-detail bg-light px-2 mt-2">
                            <i class="fa fa-file-pdf-o" aria-hidden="true"></i>
                            <span style="width: 30px;">{{ getAttachmentCount(data.row.attachmentCountWithExt,
                    "Document") }}</span>
                            <i class="fa fa-image" aria-hidden="true"></i>
                            <span style="width: 30px;">{{ getAttachmentCount(data.row.attachmentCountWithExt,
                    "Image") }}</span>
                            <i class="fa fa-video-camera" aria-hidden="true"></i>
                            <span style="width: 30px;">{{ getAttachmentCount(data.row.attachmentCountWithExt,
                    "Video") }}</span>
                        </div>
                        <div class="card-tags" v-if="(data.row.TAG_NAME != '')">
                            <div class="rounded" v-for="(tagItem, tagindex) in data.row.TAG_NAME" :key="tagindex"
                                style="background: rgb(23, 162, 184); color: rgb(233, 233, 233);"
                                v-bind:style="SetStyle(tagItem)">
                                {{ tagItem.tag_name }} <a href="javascript:;" v-bind:id="tagItem.tag_id"
                                    v-on:click="RemoveTag($event, tagItem)" class="ml-1"><i
                                        class="fa fa-close text-white"></i></a>
                            </div>
                        </div>
                    </div>
                    <div class="bottom-action" style="background:none !important;">
                        <div v-bind:class="getStatusClass(data.row.status_id, 'status_id', 'fsm')"> {{
                    data.row.status_id }}
                        </div>
                        <div class="px-2 border border-success text-center text-success d-block"
                            v-if="data.row.TICKET_AR_TYPE == 'accept' && (data.row.is_field_service == true || data.row.is_field_service == 'FSM')"
                            data-toggle-tooltip="tooltip" data-original-title="Accepted"> Accepted</div>
                        <div class="px-2 border border-danger text-center text-danger d-block"
                            v-if="data.row.TICKET_AR_TYPE == 'reject' && (data.row.is_field_service == true || data.row.is_field_service == 'FSM')"
                            data-toggle-tooltip="tooltip" data-original-title="Rejected"> Rejected</div>
                        <div class="card-action my-2">
                            <tempalte v-if="IsAcceptRejectTicket == 'yes'">
                                <a href="javascript:;" class="round-icon-small btn-success"
                                    v-if="(data.row.TICKET_AR_TYPE == '' && data.row.IS_SHOW_ACCEPT_REJECT == 1 && (data.row.is_field_service == true || data.row.is_field_service == 'FSM'))"
                                    data-toggle-tooltip="tooltip" data-original-title="Accept" v-bind:id="'scrollup'"
                                    v-on:click="AcceptRejectTicket(data.row, 'accept')">
                                    <em class="fa fa-check" aria-hidden="true"></em>
                                </a>
                                <a href="javascript:;" class="round-icon-small btn-danger"
                                    v-if="(data.row.TICKET_AR_TYPE == '' && data.row.IS_SHOW_ACCEPT_REJECT == 1 && (data.row.is_field_service == true || data.row.is_field_service == 'FSM'))"
                                    data-toggle-tooltip="tooltip" data-original-title="Reject" v-bind:id="'scrollup'"
                                    v-on:click="AcceptRejectTicket(data.row, 'reject')">
                                    <em class="fa fa-close" aria-hidden="true"></em>
                                </a>
                            </tempalte>
                            <a href="javascript:;" v-if="data.row.is_field_service == 'FSM'"
                                class="round-icon-small btn-secondary" data-toggle-tooltip="tooltip"
                                title="View On Map" v-bind:id="'scrollup'"
                                v-on:click="openAddNewEvent(data.row.ticket_id, data.row)">
                                <em class="fa fa-map-marker" aria-hidden="true"></em>
                            </a>
                            <a class="round-icon-small btn-secondary" data-toggle-tooltip="tooltip"
                                title="Edit" v-if="data.row.status_code == 'TICKET_DRAFT'"
                                @click="openaddTicket(data.row)"><i class="fa action-icon fa fa-edit"></i></a>
                            <a class="round-icon-small  btn-info" data-toggle-tooltip="tooltip"
                                title="Click Here To View Ticket" v-if="data.row.is_field_service"
                                @click="openViewTicketTab(data.row.str_ticket_id, 'Card')"><em
                                    class="fa fa-ticket"></em></a>
                            <a class="round-icon-small  btn-info" data-toggle-tooltip="tooltip"
                                title="Click Here To View Ticket" v-if="!data.row.is_field_service"
                                @click="openViewTicketTab(data.row.str_ticket_id)"><em class="fa fa-ticket"></em></a>
                            <a href="javascript:;" class="round-icon-small btn-primary" data-toggle-tooltip="tooltip"
                                title="Quick View"
                                v-on:click="openQuickViewpopup(data.row.ticket_id, data.row.str_ticket_id, data.row.ticket_category_id, data.row.is_field_service)"
                                v-bind:value="data.row.ticket_id">
                                <em class="fa fa-eye" aria-hidden="true"></em>
                            </a>
                            <a data-toggle-tooltip="tooltip" title="Click here todele teticket"
                                v-show="ShowDeleteButton(data.row)" href="javascript:;"
                                class="round-icon-small  btn-danger" v-on:click="commonDeleteData(data.row.ticket_id)"
                                id="aIndexDelete" v-bind:value="data.row.ticket_id">
                                <i class="fa fa-trash" aria-hidden="true"></i>
                            </a>
                            <a data-toggle-tooltip="tooltip" title="Click Here To Edit FollowUp"
                                v-if="data.row.status_code != 'TICKET_DRAFT'" class="round-icon-small  btn-success"
                                @click="ManageFollowUp(data.row.ticket_id, data.row)" id="ManageFollowUp"
                                v-bind:value="data.row.ticket_id">
                                <i class="fa fa-flag action-icon"></i>
                            </a>
                            <a data-toggle-tooltip="tooltip" title="Recurring Ticket View Detail"
                                v-on:click="openViewRecurringTicket(data.row)"
                                v-if="data.row.parent_recurring_ticket > 0" href="javascript:;"
                                class="round-icon-small  btn-success" id="ManageRecurringTicketwDetail"
                                v-bind:value="data.row.ticket_id">
                                <i class="fa fa-refresh action-icon"></i>
                            </a>
                        </div>
                    </div>
                </div>
            </template>
        </tg-list>
        <AssignTicket v-if="ShowAssignTicket" @closedialog='closedialog' :ticketids="TicketIds"></AssignTicket>
        <!-- <showAddTicket v-if="showAddTicketPopup" @closedialog='closedialog' :classicEditor="editor"></showAddTicket> -->

        <!-- Update StatusPOPUP -->
        <transition name="modal">
            <div class="my-popups">
                <div class="modal d-block mail-box-pop dialog" v-if="showUpdateStatusModal">
                    <div class="modal-dialog" style="max-width:500px;">
                        <div class="modal-content">
                            <div class="modal-header">
                                <slot name="header">
                                    <h5 class="modal-title">
                                        <span class="form-group">
                                            {{ $t('Update Status') }}
                                        </span>
                                    </h5>
                                </slot>
                                <button type="button" title="" id="UpdateStatus"
                                    v-on:click="showUpdateStatusModal = false" class="close"
                                    data-original-title="Close"><em class="fa fa-times"
                                        aria-hidden="true"></em></button>
                            </div>
                            <div class="modal-body">
                                <div class="row m-0 ">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-0 p-0">
                                        <div class="col-12 p-0">
                                            <Form ref="form">
                                                <div class="w-100 float-left bg-white pb-2 bordere4e4e4">
                                                    <div class="w-100 float-left">
                                                        <div
                                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                            <div class="collapse show py-0">
                                                                <div
                                                                    class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                                    <div class="form-group">
                                                                        <label>{{ $t('Status') }}:<span
                                                                                class="text-danger">*</span></label>
                                                                        <Field name="Status"
                                                                            :rules="{ 'required': true }">
                                                                            <select class="form-control StatusId"
                                                                                name="StatusId"
                                                                                v-bind:class="{ 'form-control': true }"
                                                                                v-model.trim="StatusId">
                                                                                <option value="">{{ $t('Select') }}
                                                                                </option>
                                                                                <option
                                                                                    v-for="(item, index) in StatusList"
                                                                                    v-bind:value="item.value"
                                                                                    :key="index">
                                                                                    {{ item.name }}</option>
                                                                            </select>
                                                                        </Field>

                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="col-lg-12  col-xl-12 text-right float-left p-0">
                                                            <div
                                                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                                <div class="form-group">
                                                                    <div class="search-btm-btn">
                                                                        <div
                                                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                                            <a href="javascript:;" :title="$t('Save')"
                                                                                v-on:click="SubmitTicketingUpdateStatus()"
                                                                                v-bind:class="{ 'disabled': (isSending === true) }"
                                                                                v-bind:disabled="isSending"
                                                                                class="btn btn-success mr-1"><i
                                                                                    class="fa fa-save pr-2"></i>
                                                                                {{ $t('Save') }}</a>
                                                                            <a href="javascript:;" :title="$t('Close')"
                                                                                v-on:click="showUpdateStatusModal = false"
                                                                                class="btn btn-danger"><i
                                                                                    class="fa fa-close pr-2"></i>
                                                                                {{ $t('Close') }}</a>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </Form>
                                        </div>
                                    </div>
                                    <input type="hidden" value="10" id="hdfEndIndex" />
                                    <input type="hidden" value="" id="hdnTabClick" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <!-- ASSIGN TAGS -->
        <transition name="modal">
            <div class="my-popups">
                <div class="modal d-block mail-box-pop dialog" v-if="showTags">
                    <div class="modal-dialog" style="max-width:500px;">
                        <div class="modal-content">
                            <div class="modal-header">
                                <slot name="header">
                                    <h5 class="modal-title">
                                        <span class="form-group">
                                            {{ $t('Tags') }}
                                        </span>
                                    </h5>
                                </slot>
                                <button type="button" title="" v-on:click="showTags = false" class="close"
                                    data-original-title="Close"><em class="fa fa-times"
                                        aria-hidden="true"></em></button>
                            </div>
                            <div class="modal-body">
                                <div class="row m-0 ">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-0 p-0">
                                        <div class="col-12 p-0">
                                            <Form ref="tags">
                                                <div class="w-100 float-left bg-white pb-2 bordere4e4e4">
                                                    <div class="w-100 float-left">
                                                        <div
                                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                            <div class="collapse show py-0">
                                                                <div
                                                                    class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                                    <div class="form-group">
                                                                        <label>{{ $t('Assign Tag') }}:<span
                                                                                class="text-danger">*</span></label>
                                                                        <Field name="Status"
                                                                            :rules="{ 'required': true }">
                                                                            <tags-input element-id="tags"
                                                                                v-model="selectedTags"
                                                                                v-bind:existing-tags="existingTags"
                                                                                limit="6" v-bind:allow-duplicate="false"
                                                                                hide-input-on-limit="true"
                                                                                v-bind:typeahead="true"
                                                                                placeholder="Search Tags"></tags-input>
                                                                        </Field>

                                                                    </div>
                                                                </div>
                                                                <div v-show="IsTagError"
                                                                    class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                                    <span v-for="(item, tagIndex) in TagsError"
                                                                        :key="tagIndex"
                                                                        class="col-xl-12 float-left text-danger alert-danger p-1 ">Tag:
                                                                        {{ item.TAG_NAME }} is already used in ticket:
                                                                        {{ item.TICKET_NUMBER }}</span>
                                                                </div>
                                                                <div
                                                                    class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                                                    <p class="mandatory">Maximun Six Tags Allowed</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="col-lg-12  col-xl-12 text-right float-left p-0">
                                                            <div
                                                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                                <div class="form-group">
                                                                    <div class="search-btm-btn">
                                                                        <div
                                                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                                                            <a href="javascript:;"
                                                                                class="btn btn-primary save-btn mr-2"
                                                                                v-on:click="AssignTagAction()"
                                                                                title="Save"><i
                                                                                    class="fa fa-floppy-o pr-2"></i>Save</a>
                                                                            <a href="javascript:;"
                                                                                class="btn btn-primary delete-btn"
                                                                                v-on:click="showTags = false"
                                                                                title="Cancel"><i
                                                                                    class="fa fa-times pr-2"></i>Cancel</a>

                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </Form>
                                        </div>
                                    </div>
                                    <input type="hidden" value="10" id="hdfEndIndex" />
                                    <input type="hidden" value="" id="hdnTabClick" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
        <showRejectTicketModal v-if="showRejectTicketModal" v-on:close="showRejectTicketModal = false">
        </showRejectTicketModal>

        <modalAttachment v-if="showAttachmentModal" v-on:close="showAttachmentModal = false" :id="id"
            :configdata="ConfigData" v-bind:callbackfunction="fetchRecords" :isDisabledButton="isDisabledButton"
            modulename="Ticketing" submodulecode="Ticketing">
        </modalAttachment>

        <modalReplyTicketpopup v-if="showReplyTicketModal" v-on:close="showReplyTicketModal = false" :id="id"
            :configdata="ConfigData" v-bind:callbackfunction="fetchRecords" modulename="Ticketing"
            submodulecode="Ticketing">
        </modalReplyTicketpopup>

        <AddRecurringViewModal v-if="showRecurringViewModal" v-on:close="showRecurringViewModal = false"
            :TicketId="RecurringTicketId" :status="Status"></AddRecurringViewModal>

