        DeleteTicketCard: function (item, index) {
            //debugger;
            var vm = this;
            var totlaComments = item.totalcomments;
            var loggedId = vm.userid;

            if (vm.usertype == "NU" && item.ticket_created_by == vm.userid && item.totalcomments > 0 && item.assigned_to_id == vm.userid) {
                ShowAlert(Globalize.localize('NUDeleteTicketReply'), "warning", true, Globalize.localize("Alert"));
                return false;

            }
            else if (vm.usertype == "NU" && item.ticket_created_by != vm.userid && item.assigned_to_id != vm.userid) {
                ShowAlert(Globalize.localize('NUDeleteTicketOtherUser'), "warning", true, Globalize.localize("Alert"));
                return false;
            }
            else {
                try {
                    var obj = {
                        paths: window.location.origin,
                        id: item.ticket_id,
                        controlId: $(this).attr('id'),
                        deppartmentCode: item.ticket_department_id,
                        deptId: item.department_id,
                        status: item.status_code.toLowerCase(),
                        issupportticket: item.is_support_ticket
                    };
                    var postString = JSON.stringify(obj);
                    if (obj.status == 'ticket_closed' || obj.status == 'ticket_closedlocked' || obj.status == 'ticket_cancelled' || obj.status == 'ticket_completed') {
                        ShowAlert(Globalize.localize('SorryThetickethavebeenclosed'), "warning", true, Globalize.localize("Alert"));
                        return false;
                    }
                    else {
                        debugger
                        var param = 'controller=ticketing&action=DeleteTicket';
                        DataServices.CheckPrivilege(param).then(function (response) {
                            if (response.data) {
                                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete")), function () {
                                    DataServices.DeleteTicket(postString).then(function (response) {
                                        debugger
                                        try {
                                            if (response.data.status == 'Success') {
                                                vm.ShowAlert(vm.$t("Ticket has been successfully deleted"), 'success', true, vm.$t('Alert'));
                                                vm.fetchRecords();
                                            }
                                            else {
                                                //ShowAlert(response.msg, "failure", true, Globalize.localize('Alert'));
                                                vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                                            }
                                        }
                                        catch (ex) {
                                            //return HandleErrorOccured(ex, 'deleteTicket');
                                        }
                                    });
                                });
                            }
                        });

                    }
                }
                catch (ex) { return HandleErrorOccured(ex, 'deleteTicket'); }
            }
