        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'UnPublish':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Reopen':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'On Hold':
                    returnClass = 'px-2 bg-warning text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Open':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Waiting':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'In progress':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                case 'Completed':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                case 'Cancelled':
                    returnClass = 'px-2 bg-danger text-center text-white d-block';
                    break;
                case 'Closed':
                    returnClass = 'px-2 bg-danger text-center text-white d-block';
                    break;
                case 'Closed and Locked':
                    returnClass = 'px-2 bg-danger text-center text-white d-block';
                    break;
                case 'Waiting Resolved':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                case 'Waiting Response':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        updateURLParameter(url, param, paramVal) {
            var newAdditionalURL = "";
            var tempArray = url.split("?");
            var baseURL = tempArray[0];
            var additionalURL = tempArray[1];
            var temp = "";
            if (additionalURL) {
                tempArray = additionalURL.split("&");
                for (var i = 0; i < tempArray.length; i++) {
                    if (tempArray[i].split('=')[0] != param) {
                        newAdditionalURL += temp + tempArray[i];
                        temp = "&";
                    }
                }
            }
            var rows_txt = temp + "" + param + "=" + paramVal;
            return baseURL + "?" + newAdditionalURL + rows_txt;
        },
