        fetchRecords: async function () {
            //   debugger
            this.isLoading = true;
            var vueObj = this;
            var updatedOnDate = "";
            vueObj.TotalRecords = 0;
            vueObj.isDisabledButton = false;
            if (vueObj.UpdatedOn != "") {
                updatedOnDate = moment(String(Globalize.culture().calendar.patterns.d).toUpperCase()).format("YYYY-MM-DD");
            }
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                if (vueObj.appliedFilter.length > 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }
            else {
                if (vueObj.appliedFilter.length == 0) {
                    const removeFilterindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vueObj.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = vueObj.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vueObj.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                }
            }
            var TicketText = vueObj.TicketText.split("#").join("%23").split("&").join("%26");
            var url = `priorityCode=${vueObj.Priority}&duration=${vueObj.Duration}&SupervisorId=${vueObj.Supervisor}&PageSize=${vueObj.PageSize}&PageNumber=${vueObj.PageNumber}
            &SortBy=${vueObj.SortBy}&SortExp=${vueObj.SortExp}&WorkOrderApproval=${vueObj.WorkOrderApprovalPending}
            &isAssigned=${vueObj.$route.meta.IsAssigned}&IsFormSubmitted=${vueObj.IsFormSubmitted}&TagId=${vueObj.Tags == "ALL" ? -1 : vueObj.Tags}
            &IsDepartment=${vueObj.Department}&IsCategory=${vueObj.Category}&IsContract=${vueObj.Contract}&IsProduct=${vueObj.Product}&IsOpenBy=${vueObj.UserId}
            &UpdatedOn=${updatedOnDate}&viewtickettype=${vueObj.ViewTicketType}&code=${vueObj.selectedWidgetCode}
            &ticketText=${TicketText}&IsAccepted=${vueObj.IsAccepted}
            &IsRecurring=${vueObj.Recurring}&IsOpenDate=${vueObj.OpenDate}`;
            if (!vueObj.isBlank(vueObj.searchCondition)) {
                if (this.searchCondition.includes('TICK.TICKET_NUMBER ')) {
                    url = url + "&search=" + encodeURIComponent(this.searchCondition);
                }
                else if (this.searchCondition.includes('STS.STATUS_NAME ')) {
                    url = url + "&search=" + encodeURIComponent(this.searchCondition);
                }
                else {
                    url = url + "&search=" + vueObj.searchCondition.split("&").join("%26");
                }
            }
            //   debugger
            await DataServices.GetFSMListing(url).then(function (response) {
                debugger;
                vueObj.isLoading = false;
                vueObj.TicketList = [];
                //vueObj.mapObj=response;
                vueObj.componentKey += 1;
                if (response.data.result.mapdetail[0] != undefined) {
                    vueObj.latLong = response.data.result.mapdetail[0].LATITUDE + ',' + response.data.result.mapdetail[0].LONGITUDE;
                    vueObj.mapExtentlatLong = response.data.result.mapdetail[0].LAT_LNG;
                }
                vueObj.MapType = response.data.result.MapType == undefined ? 'Google' : response.data.result.MapType.TYPE;
                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                vueObj.CompanyType = response.data.result.CompanyType;
                vueObj.CurrentPage = 1;
                vueObj.AllCount = 0;
                vueObj.CriticalCount = 0;
                vueObj.HighCount = 0;
                vueObj.MediumCount = 0;
                vueObj.LowCount = 0;
                if(vueObj.CompanyType && vueObj.CompanyType.toLowerCase() !== "normal")
                {
                    var legenderyArray = 
                    {
                        class: 'fsm-ticket colorboxsquare',
                        text: "Recurring Tickets"
                    };

                    vueObj.LegendArray.push(legenderyArray);
                }
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        var containticketnumber = false;
                        for (var i = 0; i < response.data.result.data.length; i++) {
                            response.data.result.data[i].remainingTime = '--';
                            for (var j = 0; j < vueObj.TicketNumberDataForCheckbox.length; j++) {
                                if (response.body.result.data[i].TicketNumber == vueObj.TicketNumberDataForCheckbox[j]) {
                                    containticketnumber = true;
                                    break;
                                }
                            }
                            if (containticketnumber == true) {
                                break;
                            }
                        }
                        vueObj.Headers = response.data.result.schema;
                        vueObj.ConfigData = response.data.result.configdata;
                        var data = vueObj.ConfigData;
                        $.each(data, $.proxy(function (index1, item) {
                            if (item.CORE_CONFIG_KEYx == "ShowSLATime") {
                                vueObj.SLATimeConfigValue = item.CONFIG_DATA_VALUE;
                            }
