        SetTicketListColor: function (color, name) {
            if (color == '' || color == null || typeof (color) == 'undefined') {
                color = '#28a745';
            }
            var styles = ' tr.dynamiccolor_' + name + ' > td:first-child::after { background: ' + color + ' !important }';
            var style = document.createElement('style');
            style.innerHTML = styles;
            document.head.appendChild(style);
        },
        startTimer: function (item, responseTime, resolveTime, showResponse, showResolve, SLA_Value, TicketSlaId) {
            var rtval = "--";
            var duration = 0;
            if (showResponse == "1") {
                duration = responseTime;
            }
            else if (showResolve == "1") {
                duration = resolveTime;
            }
            if (showResponse == "1" || showResolve == "1") {
                var timer = duration, hours, minutes, seconds;
                var originalValue = timer;
                var negativeTime = false;
                setInterval(function () {
                    if (SLA_Value.toLowerCase() == "no") {
                        item.remainingTime = "--";
                    }
                    else {
                        if (Math.round(timer) < 0) {
                            timer = -timer;
                            negativeTime = true;
                        }
                        var mins_num = parseFloat(timer, 10);
                        var hours = Math.floor(mins_num / 60);
                        var minutes = Math.floor((mins_num - ((hours * 3600)) / 60));
                        var seconds = Math.floor((mins_num * 60) - (hours * 3600) - (minutes * 60));

                        hours = hours < 10 ? "0" + hours : hours;
                        minutes = minutes < 10 ? "0" + minutes : minutes;
                        seconds = seconds < 10 ? "0" + seconds : seconds;

                        if (negativeTime) {
                            item.remainingTime = ("-" + hours + ":" + minutes + ":" + seconds);
                            timer = timer + 1 / 60;
                        }
                        else {
                            item.remainingTime = (hours + ":" + minutes + ":" + seconds);
                            timer = timer - 1 / 60;
                            negativeTime = false;
                            if (timer < 0) {
                                negativeTime = true;
                            }

                        }
                    }
                }, 1000);
            }
        },
        getTimerTextClass: function (value) {
            if (value != "--") {
                if (value.includes("-")) {
                    return "text-danger font-weight-bold";
                } else {
                    return "text-success font-weight-bold";
                }
            } else {
                return "";
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Accept':
                        action.isVisible = (row.TICKET_AR_TYPE == '' && row.IS_SHOW_ACCEPT_REJECT == 1 && row.is_field_service == 'FSM' || row.is_field_service == true);
                        break;
                    case 'Reject':
                        action.isVisible = (row.TICKET_AR_TYPE == '' && row.IS_SHOW_ACCEPT_REJECT == 1 && row.is_field_service == 'FSM' || row.is_field_service == false);
                        break;
                    case 'View on Map':
                        action.isVisible = row.is_field_service == 'FSM' ? true : false
                        break;
                    case 'Edit':
                        action.isVisible = row.status_code == 'TICKET_DRAFT';
                        break;
                    case 'Delete':
                        //action.isVisible = self.usertype == 'CA' || self.usertype == 'SA' || self.usertype == 'PM'
                        action.isVisible = self.ShowDeleteButton(row);
                        break;
                    case 'Add a Follow-Up':
                        action.isVisible = row.status_code != 'TICKET_DRAFT'
                        break;
                    case 'View Recurring Detail':
                        action.isVisible = row.parent_recurring_ticket == 1;
                        break;
                    case 'Reopen':
                        action.isVisible = row.status_code === 'Ticket_completed' || row.status_code === 'TICKET_CLOSED';
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        AcceptRejectTicket: function (item, type) {
            var vm = this;
            var msg = "";
            var alertTitle = "";
            if (type == "Accept") {
                msg = vm.$t('Are you sure you want to Accept the Ticket?'), true, vm.$t('Alert');
                alertTitle = vm.$t('Accept');
            }
            else {
                msg = vm.$t('Are you sure you want to Reject the Ticket?'), true, vm.$t('Alert');
                alertTitle = vm.$t('Reject');
            }
            if (typeof (item) === "string") {
                item = JSON.parse(item);
            }
            vm.confirmR(msg, true, alertTitle, function () {
                if (type == "reject") {
                    vm.SelectedItem = item;
                    vm.showRejectTicketModal = true;
                }
                else {
                    vm.AcceptRejectAction(item, type, "");
                }
            });
        },

        AcceptRejectAction: function (item, type, comment) {
            var vm = this;
            var postString = {
                TicketId: item.ticket_id,
                Type: type,
                Comment: comment
            };
            var objectPostString = JSON.stringify(postString);
            DataServices.AcceptRejectTicket(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1") {
                        vm.fetchRecords();
                        if (type == "reject") {
                            vm.ShowAlert(vm.$t('Ticket Rejected Successfully'), "success", true, vm.$t('Alert'));
                            vm.showRejectTicketModal = false;
                        }
                        else {
                            vm.ShowAlert(vm.$t('Ticket Accepted Successfully'), "success", true, vm.$t('Alert'));
                        }
                    }
                    else if (response.data.code == "2") {
                        vm.ShowAlert(vm.$t('Ticket AcceptRejectDenied'), "warning", true, vm.$t('Warning'));
                        vm.showRejectTicketModal = false;
                    }
                    else {
                        if (type == "reject") {
                            vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                        }
                    }
                }
                else {
                    if (type == "reject") {
                        vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                    }
                    else {
                        vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Ticket')], [vm.$t('Reject')]), "failure", true, vm.$t('Alert'));
                    }
                }
            }, function (response) {
            });
        },
