                    break;
                case 'ManageFollowUp':
                    this.ManageFollowUp(item.ticket_id, item);
                    break;
                case 'openAddNewEvent':
                    this.openAddNewEvent(item.ticket_id, item);
                    break;
                case 'openQuickViewpopup':
                    this.openQuickViewpopup(item.ticket_id, item.str_ticket_id, item.ticket_category_id, item.is_field_service);
                    break;
                case 'openViewRecurringTicket':
                    this.openViewRecurringTicket(item);
                    break;
                case 'AcceptRejectTicket':
                    this.AcceptRejectTicket(item, event.currentTarget.innerText);
                    break;
                case 'openViewTicketTab':
                    this.openViewTicketTab(item.str_ticket_id, 'List');
                    break;
                case 'MarkAsActive':
                    this.MarkAsActive(item.ticket_id);
                case 'CommonDeleteData':
                    this.commonDeleteData(item.ticket_id);
            }
        },
        ShowDeleteButton: function (item) {
            ////debugger;
            var vm = this;
            if (vm.usertype == 'CA' || vm.usertype == 'SA' || vm.usertype == 'PM' || vm.usertype == "NU") {
                if (item.ticket_created_by != vm.userid && item.status_code == 'TICKET_OPENED') {
                    return false;
                }
                if ((item.totalcomments == 0 && (item.is_field_service == false || item.is_field_service != 'FSM')) || (item.status_code == 'TICKET_DRAFT' && vm.usertype != "NU")) {
                    return true;
                }
                if (item.ticket_created_by == vm.userid && (vm.usertype == "NU" && item.status_code == 'TICKET_DRAFT')) {
                    return true;
                }
            }
            else {
                return false;
            }
