        GetDDLData: async function (type) {
            var vm = this;
            var appendingUrl = `id=&companyId=${vm.companyId}&userId=${vm.userid}&moduleName=${'FSM'}&type=${'FSM_TICKETING_STATUS'}`;
            await DataServices.GetDDLData(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.StatusList = json.DATA;
                    }
                }
            });
        },
        SubmitTicketingUpdateStatus: function () {
            var vueObj = this;
            this.$refs.form.validate().then((result) => {
                if (result) {
                    //
                    var obj = {
                        CompanyId: vueObj.companyId,
                        UserId: vueObj.userid,
                        TicketIds: vueObj.TicketIds,
                        StatusId: vueObj.StatusId,
                    }
                    var objectPostString = JSON.stringify(obj);
                    vueObj.isSending = true;
                    DataServices.TicketingUpdateStatus(objectPostString).then(function (response) {

                        if (response.data != null && response.data != "") {

                            if (response.data.status == "Success") {
                                //
                                if (response.data.result != "1000" && response.data.result.MSG == "SUCCESS") {
                                    $("input:not(.chkAll):checkbox:checked").prop('checked', false);
                                    vueObj.$emit('close');
                                    vueObj.isSending = false;
                                    vueObj.showUpdateStatusModal = false;
                                    vueObj.fetchRecords();
                                    vueObj.ShowAlert(vueObj.$t('UpdatedSuccess', [vueObj.$t("Status")]), "success", true, vueObj.$t('Alert'));
                                    //vm.ShowAlert(vm.$t("AddedSuccess",[vm.$t("Notes")]), "success", true, vm.$t("Alert"));  

                                }
                                else if (response.data.result == "1000") {
                                    vueObj.showUpdateStatusModal = false;
                                    var obj = {
                                        refId: vueObj.TicketIds,
                                        status_Id: vueObj.StatusId,
                                        req_From: 'TICKET'
                                    }
                                    var objectPostString = JSON.stringify(obj);
                                    DataServices.UpdateStatusByStatusMapping(objectPostString).then(function (response) {
                                        if (response.data != null && response.data != "") {
                                            //////debugger;
                                            if (response.data.result.MSG == "SUCCESS") {
                                                vueObj.showUpdateStatusModal = false;
                                                vueObj.fetchRecords();
                                                vueObj.ShowAlert(vueObj.$t('UpdatedSuccess', [vueObj.$t("Status")]), "success", true, vueObj.$t('Alert'));
                                            }
                                            else {
                                                vueObj.showUpdateStatusModal = false;
                                                vueObj.ShowAlert(vueObj.$t('ErrorWhileUpdating', [vueObj.$t("Status")]), "failure", true, vueObj.$t('Alert'));
                                                //ShowAlert(String.Format(Globalize.localize('ErrorWhileUpdating'), Globalize.localize('Status')), "failure", true, Globalize.localize('Alert'));
                                            }
                                        }
                                        else {
                                            vueObj.showUpdateStatusModal = false;
                                            vueObj.fetchRecords();
                                        }
                                    });
                                }
                                else {
                                    vueObj.showUpdateStatusModal = false;
                                    //vueObj.ShowAlert(vueObj.$t('ErrorWhileSavingRecords'), true, vueObj.$t('Alert'));
                                    vueObj.ShowAlert(vueObj.$t('ErrorWhileUpdating', [vueObj.$t("Status")]), "failure", true, vueObj.$t('Alert'));
                                }
                            } else {
                                vueObj.showUpdateStatusModal = false;
                                vueObj.ShowAlert(vueObj.$t('ErrorWhileUpdating', [vueObj.$t("Status")]), "failure", true, vueObj.$t('Alert'));
                                //vueObj.ShowAlert(vueObj.$t('ErrorWhileSavingRecords'), true, vueObj.$t('Alert'));
                            }
                        } else {
                            vueObj.showUpdateStatusModal = false;
                            vueObj.ShowAlert(vueObj.$t('ErrorWhileUpdating', [vueObj.$t("Status")]), "failure", true, vueObj.$t('Alert'));
                            //vueObj.ShowAlert(vueObj.$t('ErrorWhileSavingRecords'), "failure", true, vueObj.$t('Alert'));
                        }
                    });
                }
            });
        },
        DownloadExcelDataNew: function () {
            // debugger
            var vueObj = this;
            vueObj.isLoading = true;
            vueObj.PageLevelSearchCondition = "";
            if ($.trim(vueObj.LocationValue) != "") {
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                // vueObj.searchCondition += " (ISNULL(TPIN.STREET,'') %2B ' ' %2B ISNULL(TPIN.CITY,'') %2B ' ' %2B ISNULL(TPIN.COUNTY,'') %2B ' ' %2B ISNULL(TPIN.STATE,'')) LIKE '%25" + vueObj.LocationValue + "%25'";
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                //vueObj.searchCondition += " (ISNULL(TPIN.STREET,'') LIKE '%25" + vueObj.LocationValue + "%25' or " ;
                //vueObj.searchCondition +="ISNULL(TPIN.CITY,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "(ISNULL(TPIN.COUNTY,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.PLACE,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.ADDRNAME,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.ADDRFROM,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.DIGSTREET,'') LIKE '%25" + vueObj.LocationValue + "%25' OR ";
                vueObj.PageLevelSearchCondition += "ISNULL(TPIN.STATE,'') LIKE '%25" + vueObj.LocationValue + "%25' ) ";
            }
            if ($.trim(vueObj.TicketType) != "") {
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += " TPIN.TYPE_ID = " + vueObj.TicketType;
            }
            if ($.trim(vueObj.Subject) != "") {
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += " TPIN.TYPEOFWORK LIKE '%25" + vueObj.Subject + "%25'";
            }
            if ($.trim(vueObj.ticketNumber) != "") {
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += " TPIN.TICKETNUMBER LIKE '%25" + vueObj.ticketNumber + "%25'";
            }
            if ($.trim(vueObj.AssignedTo) != "") {
                if (vueObj.AssignedTo == '-1') {
                    vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                    vueObj.PageLevelSearchCondition += " isnull(TICK.Assigned_To,0)=0 ";
                } else {
                    vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                    vueObj.PageLevelSearchCondition += " TICK.Assigned_To = " + vueObj.AssignedTo;
                }
            }
            if ($.trim(vueObj.Status) != "") {
                vueObj.PageLevelSearchCondition += ($.trim(vueObj.PageLevelSearchCondition) != "") ? " AND" : "";
                vueObj.PageLevelSearchCondition += " TICK.status_id = " + vueObj.Status;
            }
            var updatedOnDate = "";
            if (vueObj.UpdatedOn != "") {
                updatedOnDate = moment(String(Globalize.culture().calendar.patterns.d).toUpperCase()).format("YYYY-MM-DD");
            }

            var TicketText = vueObj.TicketText.split("#").join("%23").split("&").join("%26");
            var url = `priorityCode=${vueObj.Priority}&SupervisorId=&PageSize=${vueObj.TotalRecords}&PageNumber=${vueObj.PageNumber}&SortBy=${vueObj.SortBy}&SortExp=${vueObj.SortExp}&WorkOrderApproval=&isAssigned=${vueObj.IsAssigned}&IsFormSubmitted=&TagId=${(vueObj.Tags == "ALL" ? -1 : vueObj.Tags)}&UpdatedOn=${updatedOnDate}&ticketText=${TicketText}`;
            if (!vueObj.isBlank(vueObj.searchCondition)) {
                url = url + "&Search=" + vueObj.searchCondition.split("&").join("%26");
            }
            DataServices.GetFSMListing(url).then(function (response) {
                //debugger
                if (response.data.status == "Success") {
                    vueObj.JSONToCSVConvertor(response.data.result, "Ticket-Detail", true);
                    vueObj.isLoading = false;
                }
            });
        },
        setexistingtags: async function (search) {
            // //debugger;
            var vm = this;
            var params = `moduleName=${'FSM'}&type=${'TAG_DATA'}`;
            await DataServices.GetDDLData(params).then(function (response) {
                if (response.status == 200) {
                    var json = response.data;
                    vm.isLoading = true;
                    var newList = [];
                    if (json != null) {
                        $.each(json.DATA, function (key, item) {
                            var obj = {
                                key: item.value, value: item.name
                            };
                            newList.push(obj);
                        });
                        vm.existingTags = newList;
                    }
                    vm.isLoading = false;
                }


            });
        },
        AssignTagAction: function () {
            var vm = this;
            vm.isLoading = true;

            vm.isSubmitted = true;
            var tagsSelected = vm.selectedTags;

            console.log(tagsSelected);

            this.$refs.tags.validate().then((result) => {
                if (result) {
                    vm.isSubmitted = false;
                    // var selectedTags = vm.selectvalue.value;
                    var postString = {
                        TicketIds: vm.TicketIds,
                        selectedtagsdata: tagsSelected
                    };
                    var objectPostString = JSON.stringify(postString);
                    DataServices.AssignTags(objectPostString).then(function (response) {
                        //debugger;
                        if (response.data != null) {
                            if (response.data.code == "1") {
                                vm.fetchRecords();
                                $("input:not(.chkAll):checkbox:checked").prop('checked', false);
                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Tags')]), "success", true, vm.$t("Alert"));
                                vm.showTags = false;
                                if (response.data.result != null && response.data.result.length > 0) {
                                    vm.IsTagError = true;
                                    var arrData = typeof response.data.result != 'object' ? JSON.parse(response.data.result) : JSONData;
                                    vm.TagsError = arrData.DATA;
                                }
                                vm.ShowTagDataList();
                            }
                            else {
                                vm.isLoading = false;
                                vm.ShowAlert(vm.$t('ErrorMessage', [vm.$t('Tag')], [vm.$t('Assigned')]), "failure", true, vm.$t("Alert"));
                            }
                        }
                        vm.isLoading = false;
                    }, function (response) {
                        vm.isLoading = false;
                    });
                }
                else {
                    vm.isLoading = false;
                    vm.ShowAlert(vm.$t("CorrectHighlightedErrors"), "warning", true, vm.$t('Alert'));
                }
            });

        },
        changeViewTicketType: function () {
            //debugger
            this.searchFunction();
            this.fetchRecords();
        },
        UserCurrentStatus: function () {
            debugger
            var vm = this;

            var previousStatus = vm.currentStatus;
            var confirmationTimeout = 5000;

            if (vm.userstatus == "Online") {
                var confirmationTimer = setTimeout(function () {
                    vm.userstatus = previousStatus;
                    clearTimeout(confirmationTimer);
                }, confirmationTimeout);

                vm.confirmR(vm.$t("TicketUserStatusChangeMessage"), true, vm.$t("Alert"), function () {
                    var url = `statusCode=${vm.userstatus}`;
                    DataServices.UpdateUserCurrentStatus(url).then(function (response) {
                        if (response.data.length > 0) {
                            vm.userstatus = response.data[0].status_code;
                        }
                        else {
                            vm.userstatus = response.data[0].status_code;
                        }
                        clearTimeout(confirmationTimer)
                    })
                });
            }
            else if (vm.userstatus != "Online" && vm.userstatus != "") {
                var url = `statusCode=${vm.userstatus}`
                DataServices.UpdateUserCurrentStatus(url).then(function (response) {
                    if (response.data.length > 0) {
                        vm.userstatus = response.data[0].status_code;
                        vm.currentStatus = vm.userstatus;
                    }
                    else {
                        vm.userstatus = response.data[0].status_code;
                    }
                })
            }
            else if (vm.userstatus == "") {
                var url = `statusCode=${vm.userstatus}`
                DataServices.UpdateUserCurrentStatus(url).then(function (response) {

                    if (response.data.length > 0) {
                        vm.userstatus = response.data[0].status_code;
                        vm.currentStatus = vm.userstatus;
                    }
                    else {
                        vm.userstatus = response.data[0].status_code;
                    }
                })
            }
        }
    }
}

